#main {
  // margin-right: 300px;
  padding: 2rem;

  @media screen and (max-width: 1199px) {
    margin-left: 0;

  }

  @media screen and (max-width: 782px) {
 
    #main-content {
      margin: 0 0 0 0 !important;
    }

  }

  &.layout-navbar {
    padding: 0;
  }

  #main-content {
    padding: 2rem;
    margin: 0 300px 0 0;

  }
}

.page-heading {
  margin:   2rem;

  h3 {
    font-weight: bold;
  }
}

.page-title-headings {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: .5rem;

  h3 {
    margin-bottom: 0;
    margin-right: 1rem;
  }

  .breadcrumb {
    margin-bottom: 0;
  }
}

a {
  text-decoration: none;
}
